<template>
  <div id="userLoginView">
    <h2 style="margin-bottom: 16px"></h2>
    <a-form
      :model="form"
      auto-label-width="auto-label-width"
      label-align="left"
      style="max-width: 480px; margin: auto"
      @submit="handleSubmit"
    >
      <a-form-item field="userAccount" label="" tooltip="">
        <a-input v-model="form.userAccount" placeholder="" />
      </a-form-item>
      <a-form-item field="userAccount" label="" tooltip="">
        <a-button
          type="primary"
          style="margin-right: 30px"
          @click="getVerifyCode"
          :disabled="isDisable"
          >{{ buttonText }}
        </a-button>
        <a-input v-model="form.code" placeholder="" />
      </a-form-item>
      <a-form-item field="userName" label="" tooltip="">
        <a-input v-model="form.userName" placeholder="" />
      </a-form-item>
      <a-form-item field="userPassword" label="">
        <a-input-password
          v-model="form.userPassword"
          placeholder=""
          tooltip="8"
        />
      </a-form-item>
      <a-form-item field="userPassword" label="">
        <a-input-password
          v-model="form.checkPassword"
          placeholder=""
          tooltip="8"
        />
      </a-form-item>
      <a-form-item style="justify-content: center">
        <div style="width: 100%; display: flex; justify-content: space-around">
          <a-button html-type="submit" style="width: 120px" type="primary"
            >
          </a-button>
          <div>
            <a-link
              style="line-height: 32px; text-decoration: underline"
              @click="router.replace('/user/login')"
              >
            </a-link>
          </div>
        </div>
      </a-form-item>
    </a-form>
  </div>
</template>

<script lang="ts" setup>
import { reactive, ref } from "vue";
import { UserControllerService } from "../../../generated";
import message from "@arco-design/web-vue/es/message";
import { useRouter } from "vue-router";

const form = reactive({
  checkPassword: "",
  userAccount: "",
  userName: "",
  userPassword: "",
  type: 1,
  code: "",
});

const buttonText = ref("");
const isDisable = ref(false);

const router = useRouter();

const validateEmail = (email: string): boolean => {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
};

const handleSubmit = async () => {
  if (
    !form.userAccount ||
    !form.userPassword ||
    !form.checkPassword ||
    !form.userName
  )
    return message.error("");
  if (!validateEmail(form.userAccount)) return message.error("");
  if (form.checkPassword != form.checkPassword)
    return message.error("");

  form.userAccount = form.userAccount.trim();
  form.userName = form.userName.trim();
  form.userPassword = form.userPassword.trim();
  form.code = form.code.trim();
  form.checkPassword = form.checkPassword.trim();

  if (form.userAccount.includes(" "))
    return message.error("");
  if (form.userName.includes(" ")) return message.error("");
  if (form.userPassword.includes(" "))
    return message.error("");
  if (form.code.includes(" ")) return message.error("");
  if (form.checkPassword.includes(" "))
    return message.error("");

  form.type = 2;
  const res = await UserControllerService.userRegisterUsingPost(form);
  if (res.code === 0) {
    message.success("");
    // 
    router.push({
      path: "/user/login",
      replace: true,
    });
  } else {
    message.error("," + res.message);
  }
};
const getVerifyCode = async () => {
  if (!form.userAccount) return message.error("");
  if (!validateEmail(form.userAccount)) return message.error("");
  // 
  form.type = 1;
  const res = await UserControllerService.userRegisterUsingPost(form);
  if (res.code === 0) {
    message.success("");
    // 
    isDisable.value = true;
  } else {
    message.error("," + res.message);
  }
};
</script>
