<template>
  <div id="questionView">
    <a-form :model="searchParams" layout="inline">
      <a-form-item field="title" label="" style="min-width: 280px">
        <a-input v-model="searchParams.title" placeholder="" />
      </a-form-item>
      <a-form-item field="tags" label="" style="min-width: 240px">
        <a-input-tag v-model="searchParams.tags" placeholder="" />
      </a-form-item>
      <a-form-item>
        <a-button type="primary" @click="doSubmit"></a-button>
      </a-form-item>
    </a-form>
    <a-divider size="0" />
    <a-table
      :columns="columns"
      :data="dataList"
      :pagination="{
        pageSize: searchParams.pageSize,
        current: searchParams.current,
        total,
        showTotal: true,
      }"
      @page-change="onPageChange"
    >
      <template #optional="{ record }">
        <a-space>
          <a-button type="primary" @click="toQuestionPage(record)"
            >
          </a-button>
        </a-space>
      </template>
      <template #tags="{ record }">
        <a-space wrap>
          <a-tag v-for="(tag, index) of record.tags" :key="index" color="green"
            >{{ tag }}
          </a-tag>
        </a-space>
      </template>
      <template #acceptRate="{ record }">
        {{
          `${
            record.submitNum
              ? ((record.acceptedNum / record.submitNum) * 100).toFixed(0)
              : "0"
          } %
          (${record.acceptedNum} / ${record.submitNum})`
        }}
      </template>
      <template #createTime="{ record }">
        {{ moment(record.createTime).format("YYYYMMDD") }}
      </template>
    </a-table>
  </div>
</template>

<script lang="ts" setup>
import { onMounted, ref, watchEffect } from "vue";
import {
  Question,
  QuestionControllerService,
  QuestionQueryRequest,
} from "../../../generated";
import message from "@arco-design/web-vue/es/message";
import { useRouter } from "vue-router";
import moment from "moment";

const dataList = ref([]);
const total = ref(0);
const searchParams = ref<QuestionQueryRequest>({
  title: "",
  tags: [],
  pageSize: 10,
  current: 1,
});

const loadData = async () => {
  const res = await QuestionControllerService.listQuestionVoByPageUsingPost(
    searchParams.value
  );
  if (res.code === 0) {
    dataList.value = res.data.records;
    total.value = res.data.total;
    // message.success("");
  } else {
    message.error("" + res.message);
  }
};
// 
watchEffect(() => {
  loadData();
});
onMounted(() => {
  // loadData();
});
const columns = [
  {
    title: "",
    dataIndex: "id",
  },
  {
    title: "",
    dataIndex: "title",
  },
  {
    title: "",
    slotName: "tags",
  },
  {
    title: "",
    slotName: "acceptRate",
  },
  {
    title: "",
    slotName: "createTime",
  },
  {
    title: "",
    slotName: "optional",
  },
];
const router = useRouter();
const onPageChange = (page: number) => {
  searchParams.value = {
    ...searchParams.value,
    current: page,
  };
};
/**
 * 
 * @param question
 */
const toQuestionPage = (question: Question) => {
  router.push({
    path: `/view/question/${question.id}`,
  });
};
/**
 * 
 */
const doSubmit = () => {
  // 
  searchParams.value = {
    ...searchParams.value,
    current: 1,
  };
};
</script>

<style scoped>
#questionView {
  max-width: 1280px;
  margin: 0 auto;
}
</style>
