<template>
  <a-row id="globalHeader" :wrap="false" align="center">
    <a-col flex="auto">
      <a-menu
        :default-selected-keys="['1']"
        :selected-keys="selectKeys"
        mode="horizontal"
        @menu-item-click="doMenuItemClick"
      >
        <a-menu-item
          key="0"
          :style="{ padding: 0, marginRight: '38px' }"
          disabled
        >
          <div class="title-bar" @click="router.push('/')">
            <img alt="oj" class="logo" src="@/assets/logo.png" />
            <div class="title"> OJ</div>
          </div>
        </a-menu-item>
        <a-menu-item v-for="item in visibleRoutes" :key="item.path">
          {{ item.name }}
        </a-menu-item>
      </a-menu>
    </a-col>
    <a-col flex="100px">
      <div>
        <a-dropdown @select="handleSelect">
          <a-avatar :style="{ backgroundColor: '#3370ff' }"
            >{{ store.state.user?.loginUser?.userName }}
          </a-avatar>
          <template #content>
            <a-doption
              >{{
                store.state.user?.loginUser?.userName === ""
                  ? ""
                  : ""
              }}
            </a-doption>
          </template>
        </a-dropdown>
      </div>
    </a-col>
  </a-row>
  <div></div>
</template>

<script lang="ts" setup>
import { routes } from "@/router/routes";
import { useRoute, useRouter } from "vue-router";
import { computed, ref } from "vue";
import { useStore } from "vuex";
import checkAccess from "@/access/checkAccess";
import { UserControllerService } from "../../generated";
import message from "@arco-design/web-vue/es/message";

const store = useStore();

const visibleRoutes = computed(() => {
  return routes.filter((item, index) => {
    // 
    if (item.meta?.hideInMenu) {
      return false;
    }
    // 
    return checkAccess(store.state.user.loginUser, item.meta?.access as string);
  });
});

const router = useRouter();
const route = useRoute();
// 
const selectKeys = ref(["/"]);
// 
router.afterEach((to) => {
  selectKeys.value = [to.path];
});
const doMenuItemClick = (key: string) => {
  router.push(key);
};
const handleSelect = async (v: string) => {
  if (v == "") {
    const res = await UserControllerService.userLogoutUsingPost();
    if (res.code === 0) {
      message.success("");
      // 
      await store.dispatch("user/getLoginUser");
      // 
      router.push("/");
    } else {
      message.error("," + res.message);
    }
  } else if (v == "") {
    router.push("/user/login?redirect=" + route.path);
  }
};
</script>

<style scoped>
.title-bar {
  display: flex;
  align-items: center;
}

.title {
  color: #444;
  margin-left: 16px;
}

.logo {
  height: 48px;
}
</style>
