<template>
  <div id="userLoginView">
    <h2 style="margin-bottom: 16px"></h2>
    <a-form
      :model="form"
      auto-label-width="auto-label-width"
      label-align="left"
      style="max-width: 480px; margin: auto"
      @submit="handleSubmit"
    >
      <a-form-item field="userAccount" label="" tooltip="">
        <a-input v-model="form.userAccount" placeholder="" />
      </a-form-item>
      <a-form-item field="userPassword" label="">
        <a-input-password
          v-model="form.userPassword"
          placeholder=""
          tooltip="8"
        />
      </a-form-item>
      <a-form-item>
        <div style="width: 100%; display: flex; justify-content: space-around">
          <a-button html-type="submit" style="width: 120px" type="primary"
            >
          </a-button>
          <div>
            <a-link
              style="line-height: 32px; text-decoration: underline"
              @click="router.replace('/user/register')"
              >
            </a-link>
          </div>
        </div>
      </a-form-item>
    </a-form>
  </div>
</template>

<script lang="ts" setup>
import { reactive } from "vue";
import { UserControllerService } from "../../../generated";
import message from "@arco-design/web-vue/es/message";
import { useRouter } from "vue-router";
import { useStore } from "vuex";

const form = reactive({
  userAccount: "",
  userPassword: "",
});

const router = useRouter();
const store = useStore();

const validateEmail = (email: string): boolean => {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
};
const handleSubmit = async () => {
  if (!form.userPassword || !form.userAccount) return message.error("");
  if (form.userAccount.length < 4) return message.error("4");
  if (!validateEmail(form.userAccount)) return message.error("");
  if (form.userPassword.length < 8) return message.error("8");
  form.userAccount = form.userAccount.trim();
  form.userPassword = form.userPassword.trim();
  if (form.userAccount.includes(" "))
    return message.error("");
  if (form.userPassword.includes(" "))
    return message.error("");
  const res = await UserControllerService.userLoginUsingPost(form);
  if (res.code === 0) {
    message.success("");
    // 
    store.dispatch("user/getLoginUser");
    // 
    router.push({
      path: "/",
      replace: true,
    });
  } else {
    message.error("," + res.message);
  }
};
</script>
