<template>
  <div id="addQuestionView">
    <h2>{{ updatePage ? "" : "" }}</h2>
    <a-form :model="form" label-align="left">
      <a-form-item field="title" label="">
        <a-input v-model="form.title" placeholder="" />
      </a-form-item>
      <a-form-item field="tags" label="">
        <a-input-tag
          v-model="form.tags"
          allow-clear
          placeholder=""
        ></a-input-tag>
      </a-form-item>
      <a-form-item field="answer" label="">
        <md-editor
          :handle-change="answerHandler"
          :value="form.answer"
          style=""
        />
      </a-form-item>
      <a-form-item field="content" label="">
        <md-editor :handle-change="contentHandler" :value="form.content" />
      </a-form-item>
      <a-form-item :content-flex="false" :merge-prop="false" label="">
        <a-space direction="vertical" style="min-width: 480px">
          <a-form-item field="judgeConfig.timeLimit" label="">
            <a-input-number
              v-model="form.judgeConfig.timeLimit"
              mode="button"
              placeholder=""
              size="large"
            />
          </a-form-item>
          <a-form-item field="judgeConfig.stackLimit" label="">
            <a-input-number
              v-model="form.judgeConfig.stackLimit"
              mode="button"
              placeholder=""
              size="large"
            />
          </a-form-item>
          <a-form-item field="judgeConfig.memoryLimit" label="">
            <a-input-number
              v-model="form.judgeConfig.memoryLimit"
              mode="button"
              placeholder=""
              size="large"
            />
          </a-form-item>
        </a-space>
      </a-form-item>
      <a-form-item :content-flex="false" label="">
        <a-form-item
          v-for="(judgeCaseItem, index) of form.judgeCase"
          :key="index"
          :field="`form.judgeCase[${index}].input`"
          no-style
        >
          <a-space direction="vertical" style="min-width: 480px">
            <a-form-item
              :key="index"
              :field="`form.judgeCase[${index}].input`"
              :label="`-${index}`"
            >
              <a-input
                v-model="judgeCaseItem.input"
                placeholder=""
              />
            </a-form-item>
            <a-form-item
              :key="index"
              :field="`form.judgeCase[${index}].input`"
              :label="`-${index}`"
            >
              <a-input
                v-model="judgeCaseItem.output"
                placeholder=""
              />
            </a-form-item>
            <a-button
              status="danger"
              style="margin-bottom: 12px"
              @click="handleDelete(index)"
            >
              
            </a-button>
          </a-space>
        </a-form-item>
        <div style="margin-top: 32px">
          <a-button status="success" type="outline" @click="handleAdd">
            
          </a-button>
        </div>
      </a-form-item>
      <div style="margin-top: 16px"></div>
      <a-form-item>
        <a-button style="min-width: 200px" type="primary" @click="doSubmit"
          >
        </a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script lang="ts" setup>
import { onMounted, ref } from "vue";
import MdEditor from "@/components/MdEditor.vue";
import { QuestionControllerService } from "../../../generated";
import message from "@arco-design/web-vue/es/message";
import { useRoute } from "vue-router";

let form = ref({
  title: "",
  tags: [],
  answer: "",
  content: "",
  judgeCase: [
    {
      input: "",
      output: "",
    },
  ],
  judgeConfig: {
    memoryLimit: 1000,
    stackLimit: 1000,
    timeLimit: 1000,
  },
});
const answerHandler = (value: string) => {
  form.value.answer = value;
};

const route = useRoute();

// 
const updatePage = route.path.includes("update");

const contentHandler = (value: string) => {
  form.value.content = value;
};
const handleAdd = () => {
  form.value.judgeCase.push({
    input: "",
    output: "",
  });
};

const loadData = async () => {
  let id = route.query.id;
  if (!id) return;
  const res = await QuestionControllerService.getQuestionByIdUsingGet(
    id as any
  );
  if (res.code === 0) {
    form.value = res.data as any;
    //   tags
    if (!form.value.tags) {
      form.value.tags = [];
    } else {
      // JSON 
      form.value.tags = JSON.parse(form.value.tags as any);
    }
    //   judgeConfig
    if (!form.value.judgeConfig) {
      form.value.judgeConfig = {
        memoryLimit: 1000,
        stackLimit: 1000,
        timeLimit: 1000,
      };
    } else {
      form.value.judgeConfig = JSON.parse(form.value.judgeConfig as any);
    }
    //   judgeCase
    if (!form.value.judgeCase) {
      form.value.judgeCase = [
        {
          input: "",
          output: "",
        },
      ];
    } else {
      form.value.judgeCase = JSON.parse(form.value.judgeCase as any);
    }
  } else {
    message.error("" + res.message);
  }
};

onMounted(() => {
  if (updatePage) loadData();
});

const handleDelete = (index: number) => {
  form.value.judgeCase.splice(index, 1);
};
const doSubmit = async () => {
  if (updatePage) {
    // 
    const res = await QuestionControllerService.updateQuestionUsingPost(
      form.value
    );
    if (res.code === 0) {
      message.success("");
    } else {
      message.error("" + res.message);
    }
  } else {
    // 
    const res = await QuestionControllerService.addQuestionUsingPost(
      form.value
    );
    if (res.code === 0) {
      message.success("");
    } else {
      message.error("" + res.message);
    }
  }
};
</script>

<style scoped>
#addQuestionView {
}
</style>
